.Thumbnail {
    opacity: 1;
    display: flex;
    flex-direction: column;
    margin: 10px;
    max-width: 100%;

    color: white;
    text-decoration: none;
    background-color: #242424;
    border: 2px solid #3c3c3c;
    border-radius: 15px;
    -webkit-box-shadow: 0px 0px 45px -35px rgb(0 0 0 / 90%);
    -moz-box-shadow: 0px 0px 45px -35px rgb(0 0 0 / 90%);
    box-shadow: 0px 0px 45px -35px rgb(0 0 0 / 90%);
    transition: 0.5s ease-in-out;

    box-sizing: border-box;
}

.Thumbnail.hide {
    opacity: 0;
    pointer-events: none;
}

.ThumbnailImage {
    min-height: 180px;
    overflow: hidden;
    background-position: center;
    background-size: cover;
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
    background-color: #3c3c3c;
}

.ThumbnailText {
    margin-top: 10px;
    margin-bottom: 5px;
    margin-left: 15px;
    margin-right: 15px;
}

.ThumbnailText h3 {
    margin: 0;
    margin-bottom: 5px;
}

.ThumbnailText p {
    margin: 0;
    margin-bottom: 5px;
}

.ThumbnailTags {
    margin: 5px;
    margin-bottom: 10px;
    margin-top: auto;
    display: flex;
    overflow-x: scroll;
}

.ThumbnailTags::-webkit-scrollbar {
    display: none;
}

.ThumbnailTags {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}

@media only screen and (min-width: 600px) {
    .Thumbnail {
        flex: 1 45%;
        max-width: 48%;
    }
}

/* Anything bigger than a tablet screen */
@media only screen and (min-width: 1100px) {
    .Thumbnail {
        flex: 1 30%;
        max-width: 32%;
    }
}
