.App-header {
	z-index: 1;
	background-color: #dd5405;
	min-height: 50px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	padding-right: 10px;
	padding-left: 10px;
	color: white;
}

.App-header-text {
	font-weight: bold;
	color: white;
	text-decoration: none;
}

.App-header-text {
	margin-bottom: 2px;
	font-size: 22pt;
}

.hamburger-react {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.dropDown {
	display: flex;
	background-color: #eb691e;
	margin: 0;
	padding: 0;
	max-height: 200px;
	overflow: hidden;
	transition-duration: 2s;
	transition-timing-function: linear;
	-webkit-transition: max-height 0.2s ease-in-out;
	-moz-transition: max-height 0.2s ease-in-out;
	-o-transition: max-height 0.2s ease-in-out;
	transition: max-height 0.2s ease-in-out;
}

.slideUp {
	max-height: 0px;
}

.slideDown {
	max-height: auto;
}

.dropDown {
	width: 100%;
	margin: 0;
	padding: 0;
	height: auto;
	flex-direction: column;
}

.dropDown a {
	padding-left: 15px;
	padding-right: 15px;
	height: 50px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
	cursor: pointer;
	color: white;
	text-decoration: none;
	transition: 0.3s ease;
}

.dropDown a:hover {
	background-color: #dd5405;
}

.dropDown a p {
	margin-left: 8px;
}

/* ------------------------------------- */
/* Anything bigger than a phone */
@media only screen and (min-width: 600px) {
	.hamburger-react {
		display: none;
	}

	.dropDown {
		margin-top: -50px;
		flex-direction: row;
		justify-content: flex-end;
		background-color: #dd5405;
	}

	.dropDown a:hover {
		background-color: #eb691e;
	}

	.slideUp {
		max-height: 50px !important;
	}
}

