.MainCon {
	line-height: 0px;
	padding-bottom: 0px;
	padding-top: 0px;
	display: flex;
	flex-direction: row;
	width: fit-content;
	align-items: center;
	white-space: nowrap;
	border-radius: 80px;
	border-style: solid;
	border-width: 2px;
	padding-left: 10px;
	padding-right: 10px;
	margin-top: 2px;
	margin-bottom: 2px;
	margin-left: 5px;
	margin-right: 5px;
	font-weight: bold;
	transition: 0.3s ease;
	cursor: pointer;
}

.tagButton {
	margin-left: 7px;
	margin-right: -5px;
	background-color: rgb(204, 33, 33) !important;
	width: 10px;
	height: 10px;
	border-radius: 100px !important;
	font-size: 7pt !important;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px !important;
}
