.ProjecConCon {
	margin-bottom: 35px;
	overflow: hidden;
	text-decoration: none;
	color: white;
	transition: 1s ease-in-out;
}

.ProjecConCon h2 {
	margin-right: 15px;
	margin-left: 15px;
	margin-top: 10px;
	margin-bottom: 0px;
	width: 100vw;
}

.ProjecConCon .projects {
	display: flex;
	flex-direction: column;
	margin-bottom: 25px;
}

.loadmore {
	margin-top: 25px;
	margin-bottom: 25px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media only screen and (min-width: 600px) {
	.ProjecConCon {
		margin: 20px;
		margin-top: 0px;
	}

	.ProjecConCon .projects {
		flex-wrap: wrap;
		flex-direction: unset;
		justify-content: left;
		margin-left: -10px;
		margin-right: -10px;
	}

	.ProjectCon {
		margin-left: 10px;
		margin-right: 10px;
		align-self: flex-start;
		max-width: 45vw;
	}
}

/* Anything bigger than a tablet screen */
@media only screen and (min-width: 1000px) {
	.ProjectCon {
		max-width: 420px;
	}
}
